* {
  margin: 0;
  box-sizing: 0;
  padding: 0;
  outline: none;
}

body {
  --white: #fff;
  --red: #e61400;
  --yellow: #ffcc00;
  --black: #2b2b2b;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}